import React, {Component} from "react";
import {HashSpiner} from './Spiners';
import HashLoader from "react-spinners/HashLoader";

let quotes = [
    "Bn c bit Lavenes Confession c thit k v lp trnh bi Nhats Devil ?",
    "Bn c th upload nh cho confess ca mnh y!",
    "Nn nh cc ad s khng ng cc confess mang tnh cht h da, chi ln nh!",
    "Theo cc s kin trong nm Confession s c update tnh nng tng ng  mi ngi s ng!",
    "Bn c th like v comment vo bi confession trn Web y!",
    "Winner Winner Chicken Dinner ",
]

class SplashScreen extends Component {
    render() {
        return(   
        <div className="splash_screen">
            <HashLoader
              size={50}
              color={"#2484FF"}
              loading={true}
            />
            <h1>{quotes[Math.floor(Math.random() * quotes.length)]}</h1>
        </div>);
    }
}

export {
    SplashScreen
}